//TABLE VARS



function parseTarif(tarifStr) {
    let resStr
    if (     /Серт/i.test(tarifStr) ) return 'ПР Серт.'
    if ( /1\.5 мин/i.test(tarifStr) ) return 'ПР доп. 1.5'

    if ( /Спорт/i.test(tarifStr) ) {
        resStr = `Спорт ${/\d+.*\d+/.exec(tarifStr)}`
        if (  /ночн/.test(tarifStr) ) return resStr += ' Н'
        if ( /прайм/.test(tarifStr) ) return resStr += ' PR'
        if (  /Борз/.test(tarifStr) ) return resStr =  ' БЛЮкэмп'

        return resStr
    }

    if ( /Pro /i.test(tarifStr) ) {
        resStr = 'Sport ' + /\d+/.exec(tarifStr)
        if ( /night/.test(tarifStr) ) return resStr += ' N'
        if (  /peak/.test(tarifStr) ) return resStr += ' PR'

        return resStr
    }

    if ( /^\d+ мин/.test(tarifStr) ) return 'ПР '
    if ( /-\s*20%$/i.test(tarifStr)) return 'ПР -20%'
    if ( /благотв/i.test(tarifStr) ) return 'Бл-ть'

    return tarifStr.substr(0, 15)
}

export function proceedSourceBookingsArr(bookingsArr) {
    const API_Map       = APP_CONSTANTS.bookingsAPIDataMap
    const VIEW_Map      = APP_CONSTANTS.bookingsViewPosArr
    const ViewTimeCI    = VIEW_Map.indexOf('timeCI')

    let instrRequests   = {}
    let resultArr = bookingsArr.map(
        APIDataRec => {
            let curRecSlice = APIDataRec.slice()
            curRecSlice[API_Map.timeCI] = Date.parse(curRecSlice[API_Map.timeCI]) - _Date.GMToffset
            if (curRecSlice[API_Map.status] === 'Запрос') {
                instrRequests[ curRecSlice[API_Map.bookingIdCI] ] = parseReqObj(curRecSlice, API_Map)
            }
            return VIEW_Map.map( field => curRecSlice[ API_Map[field] ] )
        }
    ).sort( (a, b) => a[ViewTimeCI] - b[ViewTimeCI] )


    return [resultArr, instrRequests]
}
//sub Function
function parseReqObj(APIDataRec, API_Map) {
    let {time, user} = JSON.parse( APIDataRec[API_Map.history] )[0]
    return {
        id         : APIDataRec[API_Map.bookingIdCI],
        timeFlight : APIDataRec[API_Map.timeCI],
        reqTime    : time,
        user,
    }
}//sub Function



export function bookingsDataToObj(bookingsData) {
    const {timeCI, timeValCI} = APP_CONSTANTS.bookingsArrMap
    const apiSubRecStCI       = timeValCI
    const veiwSubRecStCI      = timeValCI + 1
    const [ bookingsArr,
            instrRequests ]   = proceedSourceBookingsArr(bookingsData)

    const colCnt       = bookingsArr[0].length
    const subRecColCnt = colCnt - apiSubRecStCI
    const subRecTmp    = new Array(subRecColCnt).fill('')

    const stDateN      =_Date.dateMsToDateN( bookingsArr[0][timeCI] )
    const endDateN     =_Date.dateMsToDateN( bookingsArr[bookingsArr.length-1][timeCI] )+1

    const RES_BOOKING_DATA = {}
    let RES_B_OBJ          = initResObj(stDateN, endDateN)

    for (var i = 0; bookingsArr[i+1]; i++) {
        var curTime    = bookingsArr[i][timeCI]
        var curTimeVal = bookingsArr[i][timeValCI]

        var [curDateN,
             curTimeN,
             curTimeIndex]            = parseCurTime(curTime)

        var timeAccum                 = curTimeVal
        var curResRec                 = makeResRec(curTimeN, timeAccum)
            curResRec[veiwSubRecStCI] = [ bookingsArr[i].slice(apiSubRecStCI) ]


        // 1 GROUP same TIMESLOTS
        groupTimeSlots(curResRec) //adj {i. timeAccum, curResRec}

        RES_B_OBJ[curDateN][curTimeIndex] = curResRec

        // 2 SPREADING EXCEEDED TIME on next slots
        spreadOverTime(RES_B_OBJ)
    }//END for bookings rec

    //delete last/incomplete dayObjs
    delete RES_B_OBJ[endDateN]
    if (curDateN > stDateN) {
        delete RES_B_OBJ[curDateN]
    } else console.log('last date with data = st.date, the last date obj incomplete!')

    RES_BOOKING_DATA.arr   = bookingsArr
    RES_BOOKING_DATA.obj   = RES_B_OBJ
    RES_BOOKING_DATA.reqs  = instrRequests
    return RES_BOOKING_DATA



        //SUB Function
        function groupTimeSlots() {
            while ( bookingsArr[i+1]?.[timeCI] == curTime) {
                timeAccum += bookingsArr[i+1][timeValCI]
                curResRec[veiwSubRecStCI].push(bookingsArr[i+1].slice(apiSubRecStCI))
                i++
            }
            curResRec[timeValCI] = timeAccum
        }

        //SUB Function
        function spreadOverTime() {
            while (timeAccum > 30) {
                curResRec[timeValCI] = 30
                timeAccum           -= 30
                curTime             += _Date.m30;
                [curDateN,
                 curTimeN,
                 curTimeIndex]       = parseCurTime(curTime)

                curResRec            = makeResRec(curTimeN, timeAccum)
                curResRec[veiwSubRecStCI][0][1] = '-----'

                groupTimeSlots()
                RES_B_OBJ[curDateN][curTimeIndex] = curResRec
            }
            timeAccum = 0
        }//END SUB spreadovertime

        function makeResRec(curTime, timeAccum = 0) {
            let curResRec                 = []
                curResRec[timeCI]         = curTime
                curResRec[timeValCI]      = timeAccum
                curResRec[veiwSubRecStCI] = [subRecTmp.slice()]
            return curResRec
        }

        //SUB Function
        function initResObj(stDateN, endDateN) {
            var  resObj = {}
            for (let dateN = stDateN; dateN <= endDateN; dateN++) {
                resObj[dateN] = {}
                TIME_SLOTS.forEach(slot => {
                    resObj[dateN][slot[0]/_Date.m30] = makeResRec(slot[0], 0)
                })
            }
            return resObj
        }

        //SUB Function
        function parseCurTime(curTime) {
            let curDateN         = _Date.dateMsToDateN( curTime )
            let curTimeN         = curTime - curDateN * _Date.hr24
            let curTimeIndex     = curTimeN / _Date.m30
            return [curDateN, curTimeN, curTimeIndex]
        }
}