// import React                         from 'react'

import { useSelector,
         useDispatch, }              from 'react-redux'

import { _Date }                     from 'JS/_Date'

import { useIntersect, evDispatch }  from 'Hooks'

import { APP_CONSTANTS,
         SELECTORS,
         tbInViewUpd_ThunkCreator, } from 'JS/store.js'



import { ADDREC_BTN,
         EDITREC_BTN, }              from './RecEdButtons'

import { COLS_CNT }                  from './BookingsTable'



const TbodyHeaderOnClik = () => evDispatch(
    {
        type: '@@ui/TBHEADER_CLICK',
        payload: ('header Click ' + Date.now())
    }
)
function TbodyHeader({ dateObj, headerStr, colsCnt }) {

    return (
        <tr id={dateObj.dateN + '_0'}
            className={dateObj.HDay ? 'newDayTr HDay' : 'newDayTr'}
        >
            <th>
                {dateObj.dayName}
                {', '}
                {dateObj.dayN}/{dateObj.monthN}
            </th>
            <td></td>
            <td colSpan={colsCnt - 1}
                onClick={TbodyHeaderOnClik}
            >
                {headerStr}
            </td>
        </tr>
    )
}

function getDateTbodyVals({ dayDataObj, dateN, ADMIN_TOGGLE, instrRequests, sidebar_viewTime }) {
    const { timeCI,
            timeValCI,
            bookingIdCI } = APP_CONSTANTS.bookingsArrMap

    const restStCI     = timeValCI + 1
    const colsCnt      = COLS_CNT
    const minFreeTime  = 2

    const nowTime      = Date.now() - _Date.GMToffset
    const curDateObj   = _Date.msToCustomDateObj(dateN * _Date.hr24)

    const tbodyId      = dateN
    const headerStr    = getHeaderStrForDay(curDateObj)


    const rowsJSXArr = []
    for (const tSlot in dayDataObj) {
        let curSlotTrJSXarr = []
        let curTSlotN  = Number(tSlot)
        let curTime    = curDateObj.timeMS + dayDataObj[tSlot][timeCI]
        let curTimeStr = _Date.msToCustomDateObj(curTime).timeStr
        let freeTime   = 30 - dayDataObj[tSlot][timeValCI]

        let instrReq         = instrRequests?.has(curTime)

        let matchSidebarTime = curTime === sidebar_viewTime

        let curRowsDataArr = dayDataObj[tSlot][restStCI]

        let rowSpan    = curRowsDataArr.length
                       + Number(freeTime >= minFreeTime && freeTime < 30)

        let curThJSX   = doThJSX(rowSpan, curTimeStr)
        let curTr0Id   = `${dateN}_${curTimeStr}`
        let trClassStr = getTrClassList({
                            freeTime, minFreeTime,
                            timeSlotN: curTSlotN,
                            curTime, nowTime,
                            instrReq,
                            matchSidebarTime,
                         })



        let subTd0_JSXArr = (freeTime >= 30)
                          ? [doFreetimeTdJSX(colsCnt)]
                          : doSubTdJSXArr(curRowsDataArr[0])


        let curTr0JSX = <tr key = {curTr0Id}
                            id  = {curTr0Id}
                            className = {trClassStr}
                        >
                            {curThJSX}{subTd0_JSXArr}
                        </tr>


        curSlotTrJSXarr.push(curTr0JSX)

        if (rowSpan > 1) {
            let subStrArr = doSubTrJSXArr(tSlot, trClassStr, curRowsDataArr.slice(1))
            curSlotTrJSXarr.push(subStrArr)
        }

        if (freeTime >= minFreeTime && freeTime < 30) {
            curSlotTrJSXarr.push(
                <tr className={trClassStr} key={"adTr" + tSlot}>
                    {doFreetimeTdJSX(colsCnt, freeTime)}
                </tr>
            )
        }

        rowsJSXArr.push(...curSlotTrJSXarr)
    } //end do Tr for tSlots

    return { colsCnt, curDateObj, tbodyId, headerStr, rowsJSXArr }


    function doThJSX(rowSpan, timeStr) {
        let ThJSX = <th rowSpan={rowSpan}>
                        {timeStr}
                        <br/>
                        <span className="tdSpan"></span>
                    </th>;
        return ThJSX;
    } //end sub for Th

    function doFreetimeTdJSX(colsCnt, freeTime) {
        const freeTimeText = `-- свободно ${freeTime ? freeTime + ' мин.' : ''} --`;
        return (
            <td key="ft" colSpan={colsCnt} className="freeTimeTd">
                <ADDREC_BTN freeTimeText={freeTimeText} />
            </td>
        );
    } //end sub fot vacantTd


    function doSubTdJSXArr(rowDataArr) {
        const bookingId = rowDataArr[bookingIdCI - 1];

        let tdJSXArr = rowDataArr
            .slice(0, colsCnt - 1)
            .map(
                (element, i) => (
                    <td key={'pf' + i} className="pf">
                        <pre>{element}</pre>
                    </td>
                )
            );
        if (ADMIN_TOGGLE && bookingId) {
            tdJSXArr[bookingIdCI - 1] = (
                <td key={'pf' + bookingIdCI - 1} className="pf">
                    <EDITREC_BTN bookingId={bookingId} />
                </td>
            );
        }
        return tdJSXArr;
    }

    function doSubTrJSXArr(tSlot, className, subDataArr) {
        let TrJSXArr = subDataArr.map(
            (rowDataArr, i) => (
                <tr key={tSlot + '_' + i} className={className}>
                    {doSubTdJSXArr(rowDataArr)}
                </tr>
            )
        );
        return TrJSXArr;
    } //end sub fot Td
} // END dayObj to TbodyJSX

function getHeaderStrForDay(dateObj) {
    let dateStr   = _Date.dateToLongStrRu(dateObj.date);
    let headerStr = `${dateStr} ${dateObj.HDay ? ' (вых)' : ' (будень)'}`;
    return headerStr;
}
function getTrClassList({ freeTime, minFreeTime, timeSlotN, curTime, nowTime, instrReq, matchSidebarTime }) {
    const trClassOptions = {
        dayNight        : (timeSlotN <= 17)         ? 'groupN'             : 'groupD',
        oddity          : (timeSlotN % 2)           ? 'odd'                : 'even',
        noTime          : (freeTime <= minFreeTime) ? 'noTime-book'        : '',
        pastTime        : (curTime < nowTime)       ? 'pastSlot'           : '',
        instrReq        : (instrReq)                ? 'instrReq'           : '',
        matchSidebarTime: (matchSidebarTime)        ? 'bt-sidebar_viewTime': '',
    }

    const trClass = Object.values(trClassOptions).reduce(
        (acc, classOpt) => acc.concat(' ', classOpt)
    )

    return trClass
}
function setTBodyIntersect({ tbodyData, dispatcher }) {
    const intersectOptions = {
        root: null,
        rootMargin: '-20% 0% -80% 0%'
    };

    const intersectFunc = (e) => {
        const {
            isIntersecting,
        } = e[0];

        if (!isIntersecting)
            return;

        dispatcher(tbInViewUpd_ThunkCreator(tbodyData));
    };

    return { intersectOptions, intersectFunc };
}



export function DateTbody({ dateN, dayDataObj, instrRequests }) {
    const dispatch = useDispatch()
    const ADMIN_TOGGLE     = useSelector(SELECTORS.userObj).role === 'admin'
    const sidebar_viewTime = useSelector(SELECTORS.sidebar_viewTime)

    const { colsCnt,
            curDateObj,
            headerStr,
            tbodyId,
            rowsJSXArr } = getDateTbodyVals({ dayDataObj, dateN, ADMIN_TOGGLE, instrRequests, sidebar_viewTime })

    const { intersectFunc, intersectOptions } = setTBodyIntersect({
        tbodyData: { headerStr, dateN },
        dispatcher: dispatch
    })

    const targetRef = useIntersect(intersectFunc, intersectOptions)

    return (
        <tbody
            ref = {targetRef}
            id  = {tbodyId}
            className = "bookingsTable"
        >
            <TbodyHeader
                dateObj   = {curDateObj}
                headerStr = {headerStr}
                colsCnt   = {colsCnt}
            />
            {rowsJSXArr}
        </tbody>
    )
}

export default DateTbody