import React,
      { memo,
        useState,
        useEffect,
        useRef,
        useLayoutEffect,
        useImperativeHandle,
        forwardRef,
        Fragment }                 from 'react'

import { useSelector,
         useDispatch,
         shallowEqual,}            from 'react-redux'

import { APP_CONSTANTS,
         APP_VARS, }               from 'JS/store'

import { _Date, }                  from 'JS/_Date'

import { usePrevious,
         useBus, }                 from 'Hooks'


import { store,
         tbodyInViewUPD,
         bookingsDayDataUPD,
         bTableDateUPD,
         testActionMD,
         tbInViewUpd_ThunkCreator,
         SELECTORS, }              from 'JS/store.js'


import { scrollToDay,
         scrollToTime, }           from './scrollServ'

import { DateTbody }               from './BT_Tbody'





const StopwatchSVG = <svg width="14" height="14" viewBox="0 0 16 16">
    <path d="M8 3.019v-1.019h2v-1c0-0.552-0.448-1-1-1h-3c-0.552 0-1 0.448-1 1v1h2v1.019c-3.356 0.255-6 3.059-6 6.481 0 3.59 2.91 6.5 6.5 6.5s6.5-2.91 6.5-6.5c0-3.422-2.644-6.226-6-6.481zM11.036 13.036c-0.944 0.944-2.2 1.464-3.536 1.464s-2.591-0.52-3.536-1.464c-0.944-0.944-1.464-2.2-1.464-3.536s0.52-2.591 1.464-3.536c0.907-0.907 2.101-1.422 3.377-1.462l-0.339 4.907c-0.029 0.411 0.195 0.591 0.497 0.591s0.527-0.18 0.497-0.591l-0.339-4.907c1.276 0.040 2.47 0.555 3.377 1.462 0.944 0.944 1.464 2.2 1.464 3.536s-0.52 2.591-1.464 3.536z">
    </path></svg>

const ADM_HEADER         = true

const BT_HEADER_USER     = [ 'Time', StopwatchSVG, 'tarif', 'Flyers', 'Notes', 'Booking №', 'paid' ]
const BT_HEADER_ADM      = [ 'mail','phone']
const BT_HEADER_ARR      = ADM_HEADER ? BT_HEADER_USER.concat(BT_HEADER_ADM) : BT_HEADER_USER
const BT_HEADER_COLS_ARR = BT_HEADER_ARR.slice(1).map( (item, i) =>
    <th key={i} className = "th-header">
        {item}
    </th>
)
export const COLS_CNT    = BT_HEADER_ARR.length

const BT_DATES_OFFSET    = APP_CONSTANTS.BT_DATES_OFFSET

const BT_BLANK_TBODY     = () => (
    <tbody className = "bt-serv-tbody">
        <tr>
            <td colSpan={COLS_CNT} className = "bt-serv-td">
                <br /><br /><br /> NO DATA <br />
            </td>
        </tr>
    </tbody>
)

const BT_EVENTS = {
    '@@ui/NAV_BTN_CLICK': (curDateN) => (e) => {
        scrollToDay(curDateN, e.payload)
    },

    '@@ui/TEST_BTN':
        (dispatch, tableDateN, endOffset) => () => {
         dispatch( bTableDateUPD(tableDateN+endOffset))
    },

    '@@ui/TEST_BTN2':
        (dispatch, newDateN) => () => {
         dispatch( tbInViewUpd_ThunkCreator(newDateN))
    },
}

const useJumpToSidebarViewTime = (sidebar_viewTime) => {
    useEffect(() => {
        if (!sidebar_viewTime) return

        const tableDateN       = SELECTORS.bTableDateN( store.getState() )
        const jumpDate         = _Date.dateMsToDateN(sidebar_viewTime)
        const isCurDateInTable = (jumpDate >= tableDateN + BT_DATES_OFFSET.stOffset)
                              && (jumpDate <= tableDateN + BT_DATES_OFFSET.endOffset)

        if ( !isCurDateInTable ) {
            store.dispatch( bTableDateUPD(jumpDate) )
        }
        const targetNode = scrollToTime(sidebar_viewTime)
    }, [sidebar_viewTime,])
}




function DayInViewDiv() {
    const dispatch      = useDispatch()
    const curTbodyInfo  = useSelector(SELECTORS.curTbodyInfo)
    const curDateN      = curTbodyInfo.dateN
    const testOnClik    = () => dispatch( testActionMD( 'HEAD DIV Click ' +Date.now() ) )

    useBus(
        '@@ui/NAV_BTN_CLICK',
         BT_EVENTS['@@ui/NAV_BTN_CLICK'](curDateN),
        [curDateN]
    )

    return (
        <div
            id        = 'curDayDiv'
            className = 'curDayDiv'
            onClick   = { testOnClik }
        >
            {curTbodyInfo.headerStr}
        </div>
    )
}
function BookingsTableHeader () {
    return (
        <tbody id="btHeaderTbody">
            <tr id="btHeaderTr" className="tableHeader">
                <th id="th0" className="th-header th0" >
                    {BT_HEADER_ARR[0]}
                </th>
                {BT_HEADER_COLS_ARR}
            </tr>
        </tbody>
    )
}

function TbodyesSet({dateNsArr}) {
    const bookingDataObj = useSelector(SELECTORS.bookingsObjDateNs(dateNsArr), shallowEqual)
    let   instrRequests  = useSelector(SELECTORS.instrRequests)

    instrRequests = instrRequests
        ? new Set(
            Object.values( instrRequests).map(instrRequest => instrRequest.timeFlight )
          )
        : null


    let   needDataFlag   = false
    const tbodiesArr = dateNsArr.map( (curDateN, i) => {
        if ( !bookingDataObj[curDateN]) {
            needDataFlag = true
            return <BT_BLANK_TBODY key = {curDateN}/>
        }

        return <DateTbody
            key           = {curDateN}
            dateN         = {curDateN}
            dayDataObj    = {bookingDataObj[curDateN]}
            instrRequests = {instrRequests}
        />
    })

    if (needDataFlag) {//debugger
     //console.log('need Data', dateNsArr[0])
    }

    return tbodiesArr
}


export function BookingsTable() {
    const {stOffset, endOffset} = BT_DATES_OFFSET

    const dispatch         = useDispatch()
    const sidebar_viewTime = useSelector(SELECTORS.sidebar_viewTime)
    const tableDateN       = useSelector(SELECTORS.bTableDateN)
    const reqDateStr       = useSelector(SELECTORS.period, shallowEqual).reqDate
    const recDateMs   = new Date(reqDateStr).getTime() || Date.now()
    const reqDateN    = _Date.dateMsToDateN(recDateMs)

    const dateNsArr   = Array.from( {length: 1 + endOffset - stOffset},
                            ( _, i) => +tableDateN + (stOffset + i)
                        )

    useEffect(() => {
        dispatch( bTableDateUPD(reqDateN) )
        scrollToDay(reqDateN)
    }, [reqDateN, dispatch])

    useJumpToSidebarViewTime(sidebar_viewTime)


    return  <>
        <DayInViewDiv />
        <table id="mainTable" className="bookingsTable">
            <BookingsTableHeader />
            <TbodyesSet dateNsArr = {dateNsArr}/>
        </table>
    </>
}

export default BookingsTable
