import {_Date,
         TIME_SLOTS, } from 'JS/_Date'



export function scrollToDay(curDateN, direction = 'cur') {
    const _defShownHour = 9
    const targetDateObj = {
        cur:   curDateN,
        prev: +curDateN - 1,
        next: +curDateN + 1
    }
    const targetDateN = targetDateObj[direction]

    const targetTime  = targetDateN   * _Date.hr24
                      + _defShownHour * 60*60*1000

    scrollToTime(targetTime)
}


export function scrollToTime(time = Date.now()) {
    const scrollOptions = {
        block: "center"
    }

    const containerNode = document.getElementById('inner')
    const parentNode    = document.getElementById('mainTable')
    if (!parentNode || !containerNode) return

    const reqDateN    = _Date.dateMsToDateN(time)
    const reqTimeSlot = TIME_SLOTS[_Date.msToSlotN(time)]
    const targetId    = `${reqDateN}_${reqTimeSlot[2]}`
    const targetNode  = document.getElementById(targetId)

    if (!targetNode) {
        const observer = observeToScroll(parentNode, targetId, scrollOptions)
        setTimeout(() => {
            observer.disconnect()
        }, 5000)
        return
    }

    const targetPosition    = targetNode.getBoundingClientRect()
    const containerPosition = containerNode.getBoundingClientRect()
        if (targetPosition.top       - containerPosition.top > 70
         && containerPosition.bottom - targetPosition.bottom > 10) return targetNode

    console.dir(`scroll to (${targetId})`)
    targetNode.scrollIntoView(scrollOptions)
    return targetNode
}


function observeToScroll(parentNode, targetId, scrollOptions) {
    parentNode = parentNode || document.getElementById('mainTable')

    const [tbodyId, trPartId] = (targetId + '_').split('_')

    const action = (node, observer) => {
        observer.disconnect()
        node.scrollIntoView(scrollOptions)

    }

    const callback = (mutationsList, observer) => {
        for (const mutation of mutationsList) {
            if (mutation.type !== 'childList') continue

            for (let node of mutation.addedNodes) {
                if (node.id != tbodyId) continue
                if (trPartId) {
                    node = document.getElementById?.(targetId)
                }
                return action(node, observer)
            }
        }
    };

    const observer = new MutationObserver(callback)
    observer.takeRecords()
    observer.observe(parentNode, { childList: true })

    return observer
}
