import { useEffect, }         from 'react'

import { _Date, }             from 'JS/_Date'

import { ftTableDateUPD, }    from 'JS/store.js'




function timeToTdId(timeMs, daysNs) {
    const dateN = _Date.dateMsToDateN(timeMs)
    const slotN = _Date.msToSlotN(timeMs)
    const dayIndex  = daysNs.indexOf(dateN)+1
    const slotIndex = slotN+1
    const tdId = dayIndex > 0
               ? 'r' + slotIndex + 'c' + dayIndex
               : null
    return tdId
}// end timeToTdId


export const useJumpToSidebarViewTime = (sideBarTime, daysNs, dispatch) => {
    let  tdBookingEd = sideBarTime
        ? timeToTdId(sideBarTime, daysNs)
        : null

    useEffect(() => {
        if (!sideBarTime) return

        if (!tdBookingEd) {
            const targetDateN = _Date.dateMsToDateN(sideBarTime)
            dispatch( ftTableDateUPD(targetDateN) )
            return null
        }

        scrollToTdId(tdBookingEd)
    }, [sideBarTime, tdBookingEd, dispatch])

    return tdBookingEd
}

function scrollToTdId(targetId) {
    const scrollOptions = {
        behavior: 'smooth',
        block   : 'center',
        inline  : 'center',
    }

    const margin_left     = 40
    const margin_right    = 0
    const margin_vertical = 20

    const containerNode = document.getElementById('inner')
    const parentNode    = document.getElementById('mainTable')
    if (!parentNode || !containerNode) return

    const targetNode = document.getElementById(targetId)
    if (!targetNode) return

    const targetPosition    = targetNode.getBoundingClientRect()
    const containerPosition = containerNode.getBoundingClientRect()

    if (   targetPosition.top       - containerPosition.top > margin_vertical
        && containerPosition.bottom - targetPosition.bottom > margin_vertical

        && targetPosition.left     - containerPosition.left > margin_left
        && containerPosition.right - targetPosition.right   > margin_right
    ) return

    console.dir(`scroll to (${targetId})`)
    targetNode.scrollIntoView(scrollOptions)

    return
}