

import { memo } from 'react'
import {
    useSelector,
    useDispatch,
}                               from 'react-redux'

import {
    APP_CONSTANTS,
    SELECTORS,
    EventUPD,
}                               from '../../index'


import { useBus,
         evDispatch, }          from 'Hooks'

//debugger
 console.log(process.env)


const BASE_PATH = process.env.NODE_ENV === 'production'
                ? process.env.PUBLIC_URL
                : ''

const SHIFTCHANGE_APP_URL = APP_CONSTANTS.shiftChangeAppUrl


const Spacer = () => <span style={{width: '2em'}}></span>


const ShiftsButton = () => {
    return(
        <button
            id        = "shiftsButton"
            className = "navButton"
            onClick   = { () => window.open(SHIFTCHANGE_APP_URL) }
        >
            <b>Shifts</b>
        </button>
    )
}


const NavButton   = ({command, mode}) => {
    const clickHandler = () => {
        evDispatch({
            type: '@@ui/NAV_BTN_CLICK',
            payload: command
        })
    }
    const btnText  = {
        prev: <b>&laquo;</b>,
        next: <b>&raquo;</b>,
    }

    return(
        <button
            id        = { command + "Button" }
            className = "navButton"
            onClick   = { clickHandler }
        >
            { btnText[command] }
        </button>
    )
}


const BottomBar = () => {

    return (
        <div id = "bottomBar" className="bottomBar">
            <ShiftsButton />
                <Spacer />
            <NavButton command="prev" mode={'...'}/>
                <Spacer />
            <NavButton command="next" mode={'...'}/>
                <Spacer />
            <TEST_BTN/>
        </div>
    )
}

export default memo(BottomBar)











function TEST_INFO() {
    let testText = useSelector( SELECTORS.testText )
    return (
        <div className="testDiv">
            {testText}
        </div>
    )
}

function TEST_BTN() {
    if ( !APP_CONSTANTS.DEFAULT_SOURCE.includes('DEV') ) return null
    const clickHandler = () => {
        evDispatch('@@ui/TEST_BTN4')
    }


    return(
        <button
            id        = "testButton"
            className = "navButton"
            onClick   = { clickHandler }
            style     = { {backgroundColor: "red", color: "white"} }
        >
            TEST/DEV
        </button>
    )
}