import { APP_CONSTANTS, }  from 'index'


export const API_FLYERS_MAP = APP_CONSTANTS.flyersAPI_dataMap


export const DEFAULT_FLYERS_VALS = {
    flyerId: '',
    fullname: '',
    lastname: '',
    firstname: '',
    secondname: '',
    email: '',
    phone: '',
    catid: 0,
    // birthdate: '',
    notes: '',
}

export const CAT_ID_MAP = {
    'Х.З.':      0,
    'ПР':        1,
    'Спорт':     2,
    'Дети':      3,
    'Сотрудник': 5,
    'Дилер':     6,
    'Иностр.':   9,
}

export const FLYERS_ED_DATACHECK = {
    flyerId:    (flyerId)    => typeof flyerId === 'number'
                                ? true
                                : console.warn('WRONG FlyerID '    + flyerId?.toString?.()),

    flyersData: (flyersData) => flyersData?.length
                                ? true
                                : console.warn('WRONG flyersData ' + flyersData?.toString?.()),
}


export function getFlyersFilter({flyersData, flyersMap = API_FLYERS_MAP, limit = 10}) {
    if ( !FLYERS_ED_DATACHECK.flyersData(flyersData) ) return

    return (testStr = ' ') => {
        testStr         = testStr.toLowerCase()
        const keyIndex  = flyersMap['fullname']
        const resFields = ['flyerId', 'fullname', 'email', 'phone']

        let filteredArr = flyersData.filter(
            record => record[ keyIndex ]
                        .toLowerCase()
                        .includes( testStr )
        ).slice(0, limit)

        let resArr  = filteredArr.map(
            record => resFields.map(
                field => record[ flyersMap[field] ]
            )
        )

        return resArr
    }
}

export function findFlyerData({flyerId, flyersData, flyersDataMap = API_FLYERS_MAP}) {
    if ( !FLYERS_ED_DATACHECK.flyersData(flyersData) ) return

    const resFields = ['flyerId', 'fullname', 'email', 'phone']
    const flyerData = flyersData.find(
        rec => rec[ flyersDataMap['flyerId'] ] === flyerId
    )

    return resFields.map(
        fieldName => [
            fieldName,
            flyerData[ flyersDataMap[fieldName] ]
        ]
    )
}