import React,
     { useState,
       Fragment }          from "react"

import { getFlyersFilter }            from "../serv"
// import PropTypes from "prop-types";




function SuggestionsListComponent({filteredSuggestions, activeSuggestion, onClick, onMouseOver, onMouseOut}) {
    if (!filteredSuggestions.length) {
        return (
            <div className="no-suggestions" >
                <em>Совпадения не найдены</em>
            </div>
        )
    }

    return (
        <ul className="suggestions">
            {filteredSuggestions.map((suggestion, index) => {
                let className
                let visualisation = suggestion[1]

                if (index === activeSuggestion) {
                    className     = "suggestion-active"
                    visualisation = <>
                        {suggestion[1]}<br />
                        {suggestion[2]}<br />
                        {suggestion[3]}
                    </>
                }

                return (
                    <li key = {suggestion[0]}
                        className    = {className}
                        onClick      = {onClick}
                        onMouseOver  = {onMouseOver}
                        onMouseOut   = {onMouseOut}

                        data-sindex  = {index}
                        data-id      = {suggestion[0]}
                        data-name    = {suggestion[1]}
                    >
                        {visualisation}
                    </li>
                )
            })}
        </ul>
    )
}


const onChoose = ({ foundId, value, setState, setFlyerInput }) => {
    value = value.replace('NEW: ', '')
    if (!value) return

    setState({
        activeSuggestion:    0,
        showSuggestions:     false,
        filteredSuggestions: [],
        userInput:           value
    })

    setFlyerInput({ flyerId: foundId, fullname: value })
    return value
}



const onFocus = e => {
    e.target.select()
}


export const Autocomplete = React.forwardRef( ( {flyersData, initValue, setFlyerInput, ONLY_REGISTRED = false}, ref ) => {
    const [state, setState] = useState({
        activeSuggestion:     0,
        filteredSuggestions: [],
        showSuggestions:  false,
        userInput:        initValue
    })

    const {
        activeSuggestion,
        filteredSuggestions,
        showSuggestions,
        userInput,
    } = state

    const flyersFilter = getFlyersFilter({
        flyersData: flyersData,
        limit: 5
    })



    const onChange  = e => {
        const userInput          = e.currentTarget.value
        const newFlyerSuggestion = ['NEW',
                                    'NEW: ' + userInput,
                                    '', '']

        const filteredSuggestions = ONLY_REGISTRED
                                  ? [...flyersFilter(userInput)]
                                  : [newFlyerSuggestion, ...flyersFilter(userInput)]

        setState({
            filteredSuggestions,
            activeSuggestion:     0,
            showSuggestions:      true,
            userInput:            e.currentTarget.value
        })
    }


    const onClick   = e => {
        let foundId = Number(e.target.dataset.id) || 'NEW'
        let value   = e.target.dataset.name
        onChoose({ foundId, value, setState, setFlyerInput })
    }
    const onKeyDown = e => {
        const { activeSuggestion, filteredSuggestions } = state
        if (e.keyCode === 13) {
            e.preventDefault()
            let choice  = filteredSuggestions[activeSuggestion]
            if(!choice) return
            let foundId = Number(choice[0]) || 'NEW'
            let value   = choice[1]
            onChoose({ foundId, value, setState, setFlyerInput })
        }
        // User pressed the up arrow
        else if (e.keyCode === 38) {
            if (activeSuggestion === 0) return
            setState( state => ({...state, activeSuggestion: activeSuggestion - 1 }) )
        }
        // User pressed the down arrow
        else if (e.keyCode === 40) {
            if (activeSuggestion - 1 === filteredSuggestions.length) {
                return
            }
            setState( state => ({...state, activeSuggestion: activeSuggestion + 1 }) )
        }
    }
    const onMouseOver = e => {
        const suggestionIndex = e.target.dataset.sindex
        e.target.className   += "suggestion-active"
        setState( state => ({...state, activeSuggestion: +suggestionIndex}) )
    }
    const onMouseOut  = e => {
        e.target.className = e.target.className.replace("suggestion-active", '')
    }





    return <Fragment>
        <input ref={ref}
            type       = "text"
            spellCheck = {false}
            onFocus    = {onFocus}
            onChange   = {onChange}
            onKeyDown  = {onKeyDown}
            placeholder = {initValue}
            value       = {userInput}
        />
        {
            (showSuggestions && userInput) &&
            <SuggestionsListComponent
                filteredSuggestions = {filteredSuggestions}
                activeSuggestion    = {activeSuggestion}
                onClick             = {onClick}
                onMouseOver         = {onMouseOver}
                onMouseOut          = {onMouseOut}
            />
        }
    </Fragment>
})

export default Autocomplete