const DEFAULT_SOURCE = APP_CONSTANTS.DEFAULT_SOURCE

//TEST_MODE //TEST_MODE //TEST_MODE
const TEST_MODE = 0
//TEST_MODE //TEST_MODE //TEST_MODE


const _defValidation = (fetchResult) =>
    !!( fetchResult.status == 200
     && fetchResult.content )

const _defValidationErrorHandler = (command, responseObj, responseData) => {
    responseObj.status = 'ERROR: ' + responseData?.status
    try {
        responseObj.data = JSON.parse(responseData)
    } catch (error) {
        responseObj.data = responseData
    }
    console.warn(`API_${command} answer is INVALID!`, responseObj)
    return responseObj
}

const _defModification = (responseData) => responseData.content


async function API_REQUEST({ command,
                             requestObj   = {},
                             source       = DEFAULT_SOURCE,
                             validation   = _defValidation,
                             modification = _defModification, }) {

    console.log(`API_${command}`, requestObj)
    const responseObj = {status: 'LOADING', data: null}

    const USER   = getUser()

    let url, params, options

    switch (source) {
        case 'GAS_DEV_BS_URL':
        case 'GAS_MASTER_BS_URL':
            url     = APP_CONSTANTS[source]
            params  = {
                id: {
                    nick: USER.name,
                    mail: USER.mail,
                    ps: USER.ps,
                },
                command,
                data: requestObj
            }

            params.id   = JSON.stringify(params.id)
            params.data = JSON.stringify(params.data)
            options     = {'method': 'GET'}
          break
    }

    let responseData   = await fetchAPI(url, {params, options})
    if ( !validation(responseData) ) return _defValidationErrorHandler(command, responseObj, responseData)

    responseObj.status = 'OK'
    responseObj.data   = modification(responseData)

    return responseObj
}





export async function REQ_GETBOOKINGS({ stDate, endDate, days=5,
                                        validation, modification, source}) {

    stDate   = new Date(stDate)
    endDate  = new Date(endDate)

    if ( isNaN(stDate) )   stDate = new Date()
    if ( isNaN(endDate) ) {
        endDate = new Date(stDate)
        endDate.setHours( (days-1)*24, 0, 0, 0)
    }

    let noLS        = true //false

    let requestObj  = {stDate, endDate}
    let command     = 'rasp'



    //TEST_MODE //TEST_MODE //TEST_MODE
    if (TEST_MODE) {
        let lsData = API_GET_LSBOOKINGS()
        if (lsData) return
        noLS          = false
        // requestObj  = {amount: 200}
        // command     = 'GET_XLAST_BOOKINGS'
    }
    //TEST_MODE //TEST_MODE //TEST_MODE

    const responseObj = await API_REQUEST({requestObj, command, validation, modification, source})

    if (responseObj.status === 'OK') {
        let bookingsData = responseObj.data;
        let bookingsObj  = bookingsDataToObj(bookingsData)
        return saveBookings(bookingsObj, noLS)
    }
    console.log( 'ERROR IN REQ_GETBOOKINGS!')
    return null
}//end getBkngD


export async function getLastUpdate({validation, modification, source=DEFAULT_SOURCE, subject='bookings'}) {
    const COMMANDS = { bookings : 'GET_LAST_BOOKINGSUPD',
                       flyers   : 'GET_LAST_FLYERSUPD', }
    const command  = COMMANDS[subject]
    return await API_REQUEST({command, validation, modification, source})
}




export function saveBookings(bookingsDataObj, noLS = true) {
    if (!noLS) {
        window.localStorage.setItem('bookingsData', JSON.stringify(bookingsDataObj));
    }
    store.dispatch(bookingsDataUPD(bookingsDataObj))
}


export async function loadFlyersData() {
    let noLS       = true //false
    const command  = 'getFlyers'
    let flyersData = await API_REQUEST({command})

    if (flyersData.status === 'OK') {
        flyersData = flyersData.data;
        return saveFlyersData(flyersData, noLS)
    }
}

export function saveFlyersData(flyersData, noLS = true) {
    if (!noLS) {
        window.localStorage.setItem('flyersData', JSON.stringify(flyersData))
    }
    store.dispatch(flyersDataUPD(flyersData))
}







export async function API_CANCEL_BOOKING({bookingId}) {
    const requestObj = {bookingId}
    const command    = 'cancelBooking'

    return await API_REQUEST({requestObj, command})
}


export async function API_CHANGE_BOOKING(bookingObj) {
    const command    = 'changeBooking'

    return await API_REQUEST({requestObj: bookingObj, command})
}

export async function API_ADD_BOOKING(bookingObj) {
    const instrMode  = !!bookingObj.instrMode
    const command    = instrMode ? 'addBooking_instrMode' : 'addBooking'

    return await API_REQUEST({requestObj: bookingObj, command})
}


export async function API_ADD_FLYER(flyerObj) {
    const command = 'addFlyer'
    return await API_REQUEST({requestObj: flyerObj, command})
}


export async function HANDLE_API_BOOKING_RESPONSE(responseObj) {
    let noLS         = true
    let bookingsObj  = {}
    let bookingsData = responseObj


    if (bookingsData.status === 'OK') {
        bookingsData = bookingsData.data;
        bookingsObj  = bookingsDataToObj(bookingsData)
        // return saveBookings(bookingsData, bookingsObj, noLS);
    }
}






