
import { evDispatch } from 'Hooks'


const addRecOnClick = (e) => evDispatch({ type: '@@ui/ADDREC_BTN', payload: e.target })
export function ADDREC_BTN({ freeTimeText }) {
    return (
        <button
            className = "freeTimeButton"
            onClick   = {addRecOnClick}
        >
            {freeTimeText}
        </button>
    )
}

const editRecOnClick = (e) => evDispatch({ type: '@@ui/EDIT_BOOKING_CALL', payload: e.target.innerText })
export function EDITREC_BTN({ bookingId }) {
    return (
        <button
            className = "editRecButton"
            onClick   = {editRecOnClick}
        >
            {bookingId}
        </button>
    )
}
