/* eslint eqeqeq: "off", curly: "error" */
import { useState }               from 'react'

import { useSelector,
         useDispatch,
         shallowEqual, }          from 'react-redux'
import { setBasepath,
         useRoutes,
         navigate,
         useRedirect }            from 'hookrouter'

import { useBus,
         evDispatch }             from 'Hooks'

import { APP_CONSTANTS,
         store,
         toggleSidebarMD,
         sidebar_curObjUPD,
         toggleFlyerEditorMD,
         bTableDateUPD,
         SELECTORS }              from 'JS/store.js'

import { getFlyersData,
         getLastUpdate,
         _Date }                  from 'index'


import BottomBar                    from 'Сomponents/Bars/BottomBar.jsx'
import TopBar                       from 'Сomponents/Bars/TopBar.jsx'

import BookingsTable                from 'Сomponents/BookingsTable'
import FreetimeTable                from 'Сomponents/FreetimeTable'

import LoginForm                    from 'Сomponents/Login'
import FlyersEditor                 from 'Сomponents/FlyersEditor'
import BookingEditor, {
         BookingEditorLight,
         GET_DEFAULT_BOOKING_VALS,} from 'Сomponents/BookingEditor'

import {FetchComponent, FetchComponent2}  from '../JS/testing.js'

setBasepath(APP_CONSTANTS.BASE_PATH)

const routes = {
    '*bookings': () => ({mode: 'bookings', Content: BookingsTable}),
    '*freetime': () => ({mode: 'freetime', Content: FreetimeTable}),
    '*login':    () => ({mode: 'login',    Content: LoginForm}),

    'default':   () => ({mode: 'bookings', Content: BookingsTable}),
}

// const USER = SELECTORS.userObj( store.getState() )





function App() {
    useRedirect('/*', '/bookings')

    const USER           = useSelector(SELECTORS.userObj)
    if (!USER.role)      { navigate('/login') }
    const ADMIN_TOGGLE   = USER.role === 'admin'

    const {mode, Content}   = useRoutes(routes) || routes.default()

    const sidebar_isToggled     = useSelector(SELECTORS.sidebar_isToggled)
    const flyersData_showEditor = useSelector(SELECTORS.flyersData_showEditor)

    const [sidebarKey, setSidebarKey] = useState(0)

    useBus(
        '@@ui/ADDREC_BTN',
        (e) => {
            let TR0           = findTR0node(e.payload)
            let [dayN, slot]  = TR0.id.split('_')
            let [hours, mins] = slot.split(':')
            let  time         = dayN * 86400000 + (+mins +hours*60)*60000

            bookingEditor_UpdateTime(time)
        }
    )

    useBus(
        '@@ui/EDIT_BOOKING_CALL',
        (e) => {
            let bookingId  = Number( e?.payload )
            callBookingEditorForId(bookingId, setSidebarKey)
        }
    )

    useBus(
        '@@ui/FT_CELL_CLICK',
        (e) => {
            let time = e.payload
            bookingEditor_UpdateTime(time)
        }
    )

    useBus(
        '@@ui/HOME_BTN_click', (e) => {
            let reqDate = _Date.dateMsToDateN( Date.now() )
            store.dispatch(bTableDateUPD(reqDate))
        }
    )

    useBus(
        '@@ui/TEST_BTN4',
        (e) => {
            getLastUpdate({})
                .then(  res => console.log( (Date.now() - res.data)/1000) )
                .catch( err => console.log(err) )
        }

        // (e) => {
        //     store.dispatch()
        // }
    )


    return (
        <div className="App">
            <TopBar mode = { mode }/>
            <div id="ContentWrapperAll">
                <div id="ContentWrapperInner">
                    <div id = "inner" className="inner">
                    {/* <FetchComponent2 /> */}
                        <Content />
                    </div>
                </div>
                {sidebar_isToggled && ( ADMIN_TOGGLE
                                      ? <BookingEditor      key = {sidebarKey}/>
                                      : <BookingEditorLight key = {sidebarKey}/> )
                }
                {flyersData_showEditor && ADMIN_TOGGLE && <FlyersEditor  key = {1}/>}
            </div>
            <BottomBar />
        </div>
    )
}

export default App




function findTR0node(childNode) {
    let node = childNode
    while (node.nodeName !== 'TR') {
        node = node.parentNode
    }
    while (!node.id) {
        node = node.previousElementSibling
    }
    return node
}

function getBookingRec(bookingsArr, dataMap, bookingId) {
    let bookingRec = bookingsArr.find(element => element[dataMap['bookingIdCI']] === bookingId)
    let obj        = {}
    for (const field in dataMap) {
        obj[field.replace('CI','')] = bookingRec[ dataMap[field] ]
    }
    return obj
}

function callBookingEditorForId(bookingId, setSidebarKey) {
    let bookingsArr = store.getState().mainState.bookingsData.arr
    let arrDataMap  = APP_CONSTANTS.bookingsArrMap
    let bookingObj  = getBookingRec(bookingsArr, arrDataMap, bookingId)

    store.dispatch(sidebar_curObjUPD(bookingObj))

    setSidebarKey(key => key + 1)
    store.dispatch(toggleSidebarMD(true))
}

function bookingEditor_UpdateTime (time) {
    let activeSidebarObj = SELECTORS.sidebar_isToggled(store.getState())
                           ?   SELECTORS.sidebar_curObj(store.getState())
                           :   null
    if (activeSidebarObj?.time === time) { return }

    let bookingObj = activeSidebarObj
                   ?   {
                           type: 'UPDATE',
                           time,
                           submitOptions: {toNew: true, toMove: true},
                       }
                   :   {
                           type: 'INIT',
                           time,
                           flyerId: 'TOSET',
                           submitOptions: {toNew: true},
                       }

    store.dispatch(sidebar_curObjUPD(bookingObj))

    store.dispatch(toggleSidebarMD(true))
}











