
import {useRoutes, navigate}    from 'hookrouter'

import { useRef, useEffect }    from "react"

import { useSelector,
         useDispatch,}          from 'react-redux'

import { testActionMD,
         minTimeUPD,
         SELECTORS,}            from 'JS/store.js'

import { _Date } from "JS/_Date"
import { getPeriod,
         logOut,
         updateBookings,}       from '../../index'

import { InstrReqTab }          from './InstrReqTab'



import { useBus,
         evDispatch, }          from 'Hooks'


const modeMap = {
    'bookings': {href: './freetime', HMBtn: <HomeButton />},
    'freetime': {href: './bookings', HMBtn: <MinTimeButton />},
    'default':  {href: './bookings', HMBtn: <HomeButton />}
}

const Spacer = () => <span style={{width: '1em'}}></span>

const dateImageOptions = {
    locale: 'ru',
    localeOptions: {day: '2-digit', month: '2-digit', year: '2-digit'}
}




function ReqDateButton() {
    let { reqDate: reqDateStr } = useSelector( SELECTORS.period )
    let reqDate                 = new Date(reqDateStr)

    const dateInput = useRef()
    useEffect( () => {
        dateInput.current.onchange = e => setReqDate(e)
    }, [dateInput])


    const imageReqDateString = reqDate.toLocaleString(dateImageOptions.locale, dateImageOptions.localeOptions)
    function setReqDate(e) {
        let newReqDate = new Date(e.target?.value || e)
        getPeriod(newReqDate)
    }

    return (
        <button id="reqDateButton" className="reqDateButton">
            { imageReqDateString || '...' }
            <input
                type     = "date"
                id       = "reqDateInput"
                ref      = {dateInput}
                name     = "reqDate"
            />
        </button>
)}

function ReloadButton() {

    // useBus(
    //     '@@ui/NEXT_NAV_BTN_CLICK',
    //     () => console.dir('ReloadButton NEXT_NAV_BTN_CLICK detected')
    // )

    return(
        <button id="reloadButton"
            onClick={ updateBookings }
        >
            <b>&#08635;</b>
        </button>
)}

function LoginButton() {
    const dispatch   = useDispatch()
    const USER       = useSelector(SELECTORS.userObj)

    function loginButtonClick(e) {
        if ( e.target.innerText === 'Log in') {return}
        if ( window.confirm('выйти?') ) {
            dispatch( testActionMD( 'logOut()' ) )
            logOut()
        }
    }

    return(
        <button id="loginButton"
            onClick={(e) => loginButtonClick(e)}
        >
            {USER?.name?.slice(0, 6) || 'Log in'}
        </button>
)}




function HomeButton() {
    const homeButtonClick = () =>{
        evDispatch('@@ui/HOME_BTN_click')
    }

    return (
        <button id="homeButton"
            onClick    ={ homeButtonClick }
        >
            <b>⌂</b>
        </button>
    )
}

function MinTimeButton () {
    const dispatch  = useDispatch()
    let minTime     = useSelector( SELECTORS.minTime )
    function setNewMintime() {
        let minTime = prompt('Looking for ... (min)', 15)
        dispatch( minTimeUPD( minTime ) )
    }

    return(
        <button id="setMintimeButton"
            onClick={ setNewMintime }
        >
            {minTime + '\'' || '...'}
        </button>
    )
}



function ModeButtonRTD({href}) {
    return(
        <button
          id="routedModeButton"
          onClick={ () => {
                        navigate(href)
                        console.dir('call ' + href)
                } }
        >
            &#10059;
        </button>
    )
}


function TopBar({mode}) {
    const {HMBtn, href} = modeMap[mode] || modeMap.default

    return (
        <div className="buttonBar">
            <ReqDateButton />
                <Spacer />
            {HMBtn}
                <Spacer />
            <ReloadButton />
                <Spacer />
            <ModeButtonRTD href={href}/>
                <Spacer />
            <LoginButton />
                <Spacer />
            <InstrReqTab/>

        </div>
    )
}

export default TopBar
