import { forwardRef,
         useState, }        from 'react'
import { TIME_SLOTS }       from 'JS/_Date'
import { codeDateTime,
         parseDateTime, }   from './bedHelpers'




export const DateTimeInput = forwardRef(({ time, name, controls }, ref) => {
    const { setValue } = controls
    let dateSlotObj    = parseDateTime(time)
//debugger
 console.log(dateSlotObj)
    const onChangeLocal = (e) => {
        const changedField = e.target.name
        const changedValue = e.target.value
        dateSlotObj[changedField] = changedValue

        let newTime = codeDateTime(dateSlotObj.Flight_Date, dateSlotObj.TimeSlot)
        setValue(name, newTime)
    }

    return <>
        <label>Дата и время полёта</label>
        <div className="side-form__inlineWrapper">
            <input
                type="date"
                name="Flight_Date"
                placeholder="Flight_Date"
                value={dateSlotObj.Flight_Date}
                onChange={onChangeLocal} />

            <select
                name="TimeSlot"
                className='timeslot'
                value={dateSlotObj.TimeSlot}
                onChange={onChangeLocal}
            >
                {TIME_SLOTS.map((slot, i) => (
                    <option key={i} value={slot[2]}>{slot[2]}</option>
                ))}
            </select>
        </div>
    </>
})
