const DEFAULT_FETCH_OPT = {
        'method' : 'GET',
        'headers': { 'Content-Type': 'application/x-www-form-urlencoded' },
        // 'body'   : new URLSearchParams(),
    }

function checkStatus(response) {
    if (!response.ok) {
        return Promise.reject(new Error(response.statusText))
    }
    return Promise.resolve(response)
}

function retJSON(response) {
    const parsedData = response.json()
    return parsedData
}

function proceedJSON(parsedData) {
    let servText = parsedData?.content?.slice?.(0, 5)
                 ||JSON.stringify( parsedData ).slice(0, 70)
    console.log(`JSON fetched: ${servText} ...`)
    return parsedData
}

function errorHandling(error, setError) {
    console.log("fetch err: ", error)
    setError(error)
    return error
}


function prepareFetchOptions(sourceUrl, reqObj = {}) {
    let url = sourceUrl
    let { params, options = DEFAULT_FETCH_OPT } = reqObj

    if (options.method === 'POST') {
        for (const parameter in params) {
            options.body.append(
                parameter,
                encodeURIComponent(params[parameter]) // may be POST request dont need it!
            )
        }
    } else if (params) {
        url += '?'
        for (const parameter in params) {
            url += `&${parameter}=${encodeURIComponent(params[parameter])}`
        }
    }
    return [url, options]
}



export async function fetchAPI(sourceUrl, reqObj) {
    let [url, options] = prepareFetchOptions(sourceUrl, reqObj)
    return fetch(url, options)
        .then(checkStatus)
        .then(retJSON)
        .then(proceedJSON)
        .catch(errorHandling)
}


export async function fetchAPI_store(sourceUrl, reqObj, storeProps) {
    let [url, options] = prepareFetchOptions(sourceUrl, reqObj)
    const { dispatch, pendingUPD, errorUPD, dataUPD, } = storeProps

    const setPending   = (toggle) => {dispatch( pendingUPD(toggle) )}
    const setError     = (error)  => {dispatch( errorUPD(error) )}
    const setData      = (data)   => {dispatch( dataUPD(data) )}

    setPending(true)
    setError(undefined)
    setData(undefined)

    fetch(url, options)
        .then(checkStatus)
        .then(retJSON)
        .then(proceedJSON)
        .then(setData)

        .catch( error => errorHandling(error, setError) )

        .finally( () => setPending(false) )
}





