
import { PAYMENT_ENUM,
         TARIF_MAP,
         onKeyDown,
         closeSidebar,
         onSubmit, }       from './bedHelpers'

import { Autocomplete }    from '../FlyersEditor'

import { SubmitBlock }     from './SubmitBlock'
import { DateTimeInput }   from './DateTimeInput'
import { TimeValInput }    from './TimeValInput'



export function BookingEditorBaseForm({ formProps }) {
    const {
        handleSubmit,
        register,
        getValues,
        setValue,
        trigger,
        control,
        formState,

        setFlyerInput,
        SUBMIT_BUTTONS_TEXT,
        time,
        flyersData,
        flyerInput,
        bookingEditorModal,
    } = formProps

    return (
        <div id="sidebar"
            className = "sidebar"
            onKeyDown = {onKeyDown}
            tabIndex  = "0"
        >
            <header className="sidebar-header">
                Бронирование
                <button id="closeSidebarBtn" onClick={closeSidebar}>X</button>
            </header>



            <form
                noValidate
                className = "side-form"
                onSubmit  = {handleSubmit(onSubmit)}
            >
                <div id="bookingIdDiv" className="side-form__inlineWrapper">
                    <label id="bookingId-label">ID: </label>
                    <input {...register("bookingId")}
                        disabled
                        type        = "number"
                        placeholder = " NEW"
                    />
                </div>

                <DateTimeInput
                    {...register("time", { required: true })}
                    controls={{ setValue }}
                    time={time}
                />

                <div className="side-form__inlineWrapper">
                    <TimeValInput
                        register  = {register}
                        getValues = {getValues}
                        setValue  = {setValue}
                        trigger   = {trigger}
                    />

                    <label>
                        Тариф
                        <select {...register("tarif", { required: true })}>
                            {Object.entries(TARIF_MAP).map((tarif) => {
                                const tarifName = tarif[1]
                                const tarifId   = tarif[0]
                                return <option key={tarifId} value={tarifName}>{tarifName}</option>
                            })}
                        </select>
                        Оплата
                        <select {...register("status")}>
                            {PAYMENT_ENUM.map((option, i) => (
                                <option key={i} value={option}>{option}</option>
                            ))}
                        </select>
                    </label>
                </div>

                <label id="fullnameLabel" htmlFor="fullname">Клиент</label>
                <Autocomplete {...register("fullname", { required: true })}
                    key           = {flyerInput.fullname}
                    initValue     = {flyerInput.fullname}
                    flyersData    = {flyersData}
                    setFlyerInput = {setFlyerInput}
                />


                <label htmlFor="notes">Комментарии</label>
                <input type="text" {...register("notes", { required: false })} />


                <SubmitBlock
                    control     = {control}
                    setValue    = {setValue}
                    getValues   = {getValues}
                    formState   = {formState}
                    buttonsText = {SUBMIT_BUTTONS_TEXT}
                />
            </form>

            {bookingEditorModal &&
                <div id='bookingEditorModal'>
                    {bookingEditorModal}
                </div>}
        </div>
    )
}





