import { useEffect,
         useState }      from 'react'

import { useSelector,
         useDispatch, }  from 'react-redux'

import { useForm, }      from 'react-hook-form'




import {_Date, }         from 'JS/_Date'

import { store,
         APP_CONSTANTS,
         SELECTORS, }    from 'index'

import { API_ADD_FLYER,} from 'JS/request.mjs'


import { toggleFlyerEditorMD,
         newFlyerDataUPD,
         flyerEditorStateUPD,
         flyerEditorModalUPD, }   from 'JS/store'

import Autocomplete               from './Autocomplete'

const API_FLYERS_MAP = APP_CONSTANTS.flyersAPI_dataMap

const FORM_FIELDS    = ['flyerId', 'fullname', 'lastname', 'firstname', 'secondname', 'email', 'phone', 'catid', 'notes']

const DEFAULT_FLYERS_VALS = {
    flyerId: '',
    fullname: '',
    lastname: '',
    firstname: '',
    secondname: '',
    email: '',
    phone: '',
    catid: 0,
    notes: '',
}

const CAT_ID_MAP = {
    'Х.З.':    0,
    ПР:        1,
    Спорт:     2,
    Дети:      3,
    Сотрудник: 5,
    Дилер:     6,
    'Иностр.': 9,
}

const CAT_ID_OPTIONS = Object.keys(CAT_ID_MAP).map(
    category => <option
                    key   = {CAT_ID_MAP[category]}
                    value = {CAT_ID_MAP[category]}
                >
                    {category}
                </option>
)


function setupNewFlyer(flyerInput, setValue) {
    setValue('flyerId',  flyerInput.flyerId)
    setValue('fullname', flyerInput.fullname)

    let flyerInputData = flyerInput.fullname.split(' ')
    let fieldsToSet    = ['lastname', 'firstname', 'secondname']

    fieldsToSet.forEach(
        (field, index) => {
            const value = flyerInputData[ index ]
            if (value) {
                setValue(field, value)
            }
        }
    )
}

function setupExistingFlyer(flyersData, flyerInput, setValue) {
    const fieldsSet = ['flyerId', 'catid', 'firstname', 'secondname', 'lastname', 'email', 'phone', 'notes']
    const choosenFlyerData = flyersData.find(
        flyerRec => flyerRec[API_FLYERS_MAP['flyerId']] === flyerInput.flyerId
    )
    for (const field of fieldsSet) {
        const value = choosenFlyerData[ API_FLYERS_MAP[field] ]
        setValue(field, value)
    }
}

function addFlyer(flyerObj) {
    const reqFieldsMap = APP_CONSTANTS.flyersAPI_dataMap
    flyerObj.fullname  = `${flyerObj.lastname} ${flyerObj.firstname}`
                         + (flyerObj.secondname ? ` ${flyerObj.secondname}` : '')


    const flyerDataArr = []
    Object.entries(reqFieldsMap).forEach(
        ([fieldName, fieldIndex]) => {
            flyerDataArr[ fieldIndex] = flyerObj[fieldName]
        }
    )

     console.log('ADD_FLYER: ', JSON.stringify(flyerDataArr))
     return API_ADD_FLYER({flyerData: flyerDataArr})
}

const onSubmit = async(flyerObj, e) => {
    store.dispatch(flyerEditorModalUPD('РЕГИСТРАЦИЯ \n\n КЛИЕНТА \n\n...'))

    let addFlyerResponse = await addFlyer(flyerObj)
    let newFlyerRecord   = addFlyerResponse.data.record

    console.log('addFlyerResp: ', flyerObj, newFlyerRecord)
    store.dispatch(newFlyerDataUPD(newFlyerRecord))
    store.dispatch(flyerEditorStateUPD({
        EVENT: 'SUBMITTED',
        flyerId: newFlyerRecord[2],
        fullname: newFlyerRecord[0]
    }))

    store.dispatch(flyerEditorModalUPD('КЛИЕНТ \n\n ЗАРЕГИСТРИРОВАН'))
    await new Promise((resolve) => setTimeout(resolve, 500))
    store.dispatch(flyerEditorModalUPD(''))

    closeFlyersEditor()
}

function containerClick(e) {
    if( e?.target?.nodeName ==="HEADER" ||
        e?.target?.id === 'closeFEDBtn' ) {
            store.dispatch(flyerEditorStateUPD({EVENT: 'CANCELED'}))
            closeFlyersEditor()
    }
}

function closeFlyersEditor() {
    store.dispatch(toggleFlyerEditorMD(false))
}






export function FlyersEditor() {
    const { register,
            formState,
            trigger,
            setValue,
            handleSubmit,
            reset, }       = useForm({ defaultValues: DEFAULT_FLYERS_VALS, mode: 'all' })

    const dispatch         = useDispatch()
    const flyersData       = useSelector(SELECTORS.flyersDataRaw)
    const flyerEditorState = useSelector(SELECTORS.flyersData_editorState)
    const flyerEditorModal = useSelector(SELECTORS.flyersData_showModal)

    let [flyerInput, setFlyerInput] = useState({flyerId:'', fullname:''})

    // init FED
    useEffect( () => {
        if (flyerEditorState?.EVENT !== 'TOSET') return
        if (!flyerEditorState.fullname )         return

        setupNewFlyer( { flyerId: 'NEW',
                         fullname: flyerEditorState?.fullname },
                       setValue )

        return () => dispatch( flyerEditorStateUPD({}) )
    }, [dispatch, setValue, flyerEditorState])


    useEffect( () => {
        trigger('firstname')
        if (!flyerInput.flyerId) return
        reset()

        if (flyerInput.flyerId === 'NEW') {
            setupNewFlyer(flyerInput, setValue)
        } else {
            setupExistingFlyer(flyersData, flyerInput, setValue)
        }
    }, [flyerInput, flyersData, setValue, trigger, reset])



    return (
        <div id="flyersContainer"
            className = "flyersContainer"
            onClick   = {containerClick}
        >
            <header className="flyersForm-header">
                Данные клиента
                <button id="closeFEDBtn">X</button>
            </header>

            <form
                noValidate
                id        = "flyersForm"
                className = "flyersForm"
                onSubmit  = {handleSubmit(onSubmit)}
            >

                <input type="text" placeholder="Id" disabled {...register("flyerId", {required: true})} />


                    {flyerEditorState.fullnameActive &&
                        <label htmlFor="autocomplete">Клиент
                        <Autocomplete
                            {...register("fullname")}
                            flyersData    = { flyersData }
                            setFlyerInput = { setFlyerInput }
                            initValue     = { flyerEditorState?.fullname || '' }
                        />
                        </label>
                    }
                    <label htmlFor="lastname">Фамилия</label>
                    <input type="text" {...register("lastname", {required: true})} />

                    <label htmlFor="firstname">Имя</label>
                    <input type="text" {...register("firstname", {required: true})} />

                    <label htmlFor="secondname">Отчество</label>
                    <input type="text" {...register("secondname", {})} />
                <br /><br />
                <label htmlFor="catid">Категория клиента</label>
                <select {...register("catid", {})} >
                        {CAT_ID_OPTIONS}
                </select>
                <label>Контактные данные</label>
                <input type="email" placeholder="email" {...register("email", {})} />
                <input type="tel"   placeholder="phone" {...register("phone", {})} />



                <label htmlFor="notes">Комментарии</label>
                <input type="text" {...register("notes", {})} />
                <br /><br />

                <button
                    type     = "submit"
                    disabled = {!formState.isValid}
                >
                    Записать
                </button>

                {/* <button onClick={() => trigger("phone")}>
                trigger
                </button> */}

                {flyerEditorModal &&
                    <div id='flyerEditorModal'>
                        {flyerEditorModal}
                    </div>
                }
            </form>

        </div>
    )
}



export default FlyersEditor
