import React        from 'react'
import ReactDOM     from 'react-dom'
import { Provider}  from 'react-redux'

import { _Date } from "JS/_Date"

import { store,
         SELECTORS,
         periodUPD,
         userDataUPD }          from 'index'

import { loadFlyersData,
         REQ_GETBOOKINGS }         from 'JS/request.mjs'

import App                      from 'Сomponents/App.jsx'

function RESIZE_OPT() {
    document.documentElement.style.setProperty("--vh", window.innerHeight * 0.01 + "px");
}

function ERROR_LOGGER(err, id = 'global') {
    var stack   =  err.error && err.error.stack
    var message = `ERR_logger ${id}: \n`
                + `${ (err && err.error) || err }`
    if (stack) {
        message += '\n' + stack
    }

    if ( typeof message === 'object') {
        message = JSON.stringify(message)
    }

    console.trace(message)
    return message
}



window.addEventListener( 'error' , ERROR_LOGGER )
window.addEventListener( 'resize', RESIZE_OPT )

window.onload = start



async function start() {
    RESIZE_OPT()

    checkUser()

    renderApp()

    getPeriod()

    loadFlyersData()
}// end start

function renderApp() {
    ReactDOM.render(
        <Provider store={ store }>
            <App />
        </Provider>
        ,
        document.getElementById('root')
    )
}


export function checkUser() {
    if (localStorage.getItem('name') &&
        localStorage.getItem('role')) {

        let user = {
            name     : localStorage.getItem('name'),
            ps       : localStorage.getItem('ps'),
            mail     : localStorage.getItem('mail'),
            role     : localStorage.getItem('role'),
        }

        store.dispatch( userDataUPD( user ) )
        return user
    }
}


export function updateBookings() {
    let period = getPeriod()
    REQ_GETBOOKINGS(period)
}


function getPeriod(reqDate) {
    let period = SELECTORS.period( store.getState() )

    if (reqDate || !period.reqDate) {
        period = setNewPeriod(reqDate)
    }

    return period
}

function setNewPeriod(reqDate = new Date()) {
    let stDate         = new Date(reqDate)
        stDate.setDate(stDate.getDate() - 7) //also take data for prev day

    let endDate        = new Date(reqDate)
        endDate.setDate(endDate.getDate() + 21)

    reqDate =_Date.dateToYYYYMMDD(reqDate, '-')
    stDate  =_Date.dateToYYYYMMDD(stDate, '-')
    endDate =_Date.dateToYYYYMMDD(endDate, '-')

    store.dispatch( periodUPD( {reqDate, stDate, endDate} ) )

    REQ_GETBOOKINGS( {stDate, endDate} )

    return {
        reqDate,
        stDate,
        endDate
    }
}




export {
    start,
    getPeriod,
}