import { useEffect }    from 'react'



function LoginInfo({message = '', closeAction, timeout}) {

    useEffect(() => {
        if (!timeout || !closeAction) return
        const timerId = setTimeout(closeAction, timeout)

        return () =>  clearTimeout(timerId)
    }, [timeout, closeAction])


    let className = 'loginInfoBtn'
    if (message.includes('SUBMITTING')) className += ' hidden'
    if (message.includes('Error'))      className += ' error'


    return (
        <div
            id="loginInfo"
        >
            {message}
            <br /><br />
            <button
                onClick   = {closeAction}
                className = {className}
            >
                OK
            </button>
        </div>
    )
}

export default LoginInfo
