import { useEffect,
         useState,
         useRef,
         useMemo, }             from 'react'

import { useSelector,
         useDispatch,
         shallowEqual, }        from 'react-redux'

import { useForm, }             from 'react-hook-form'

import { SELECTORS,}            from 'index'

import { toggleFlyerEditorMD,
         flyerEditorStateUPD,
         sidebar_viewTimeUPD, } from 'JS/store'


import {_Date,
         TIME_SLOTS }           from "JS/_Date"



import { findFlyerData,
         FLYERS_ED_DATACHECK, } from '../FlyersEditor'


import { GET_INITIAL_VALUES,
         getFromWatchedMainVals,

       useUpdateValues,
       useSetInitialValues,

         handleWatch }          from './bedHelpers'

import { BookingEditorBaseForm }from './BookingEditorBaseForm'



export const SUBMIT_BUTTONS_TEXT = {
    toCancel : 'Отменить бронирование',
    toMove   : 'Перенести бронирование',
    toChange : 'Изменить бронирование',
    toNew    : 'Записать как новое'
}

const DEFAULT_BOOKING_VALS = {
    bookingId: '',
    history:   '',
    time:      0,
    timeVal:   0,
    tarif:     'ПР',
    fullname:  '',
    notes:     '',
    status:    '---',
    flyerId:   0,
    email:     '',
    phone:     '',
}

const MAIN_WATCHED_FIELDS = ['bookingId', 'flyerId', 'time', 'timeVal', 'tarif', 'status', 'notes']


const useFlyersDataUpd = ({
    dispatch, setValue, trigger,
    flyerEditorState, flyersData, flyerInput, setFlyerInput
}) => {

    useEffect( () => {
        const { EVENT, flyerId, fullname } = flyerEditorState
        if (EVENT === 'SUBMITTED') {
            setFlyerInput({ flyerId, fullname })
            return
        }
    }, [dispatch, setValue, setFlyerInput, flyerEditorState])


    useEffect( () => {
        if ( !FLYERS_ED_DATACHECK.flyersData(flyersData) ) return
        const flyerId = flyerInput.flyerId
        if (flyerId === 'TOSET') return

        if (flyerId === 'NEW') {
            setFlyerInput({ flyerId:  'TOSET'})//, fullname: ''})//flyerInput.fullname})
            dispatch( flyerEditorStateUPD({
                EVENT: 'TOSET',
                fullnameImputActive: false,
                fullname: flyerInput.fullname,
            }) )
            dispatch( toggleFlyerEditorMD(true) )
            return
        }
        if ( !FLYERS_ED_DATACHECK.flyerId(flyerId) ) return

        const flyerData = findFlyerData({flyerId, flyersData})
        flyerData.forEach(
            field => setValue(field[0], field[1])
        )
        trigger('fullname')
    }, [dispatch, setValue, trigger, flyersData, flyerInput, setFlyerInput])
}//end useFlyersDataUpd




function BookingEditor() {
    // const renderCnt          = useRendersCount()
    const dispatch           = useDispatch()
    const flyersData         = useSelector(SELECTORS.flyersDataRaw)
    const flyerEditorState   = useSelector(SELECTORS.flyersData_editorState)
    const valuesPassed       = useSelector(SELECTORS.sidebar_curObj)
    const initialValues      = useSetInitialValues( valuesPassed,
                                                    GET_INITIAL_VALUES,
                                                    DEFAULT_BOOKING_VALS )
    const bookingEditorModal = useSelector(SELECTORS.booking_showModal)

    const { register,
        control,
        trigger,
        setValue,
        getValues,
        watch,
        handleSubmit,
        formState, }      = useForm({defaultValues: initialValues, mode: 'all'})

    const [flyerInput,
           setFlyerInput] = useState({ flyerId:  initialValues.flyerId,
                                       fullname: initialValues.fullname })

    register('flyerId', { validate: value => value !== 'TOSET' })

    useFlyersDataUpd({ dispatch, trigger, setValue, setFlyerInput,
                       flyerEditorState, flyersData, flyerInput })


    const watchedMainValues = watch(MAIN_WATCHED_FIELDS)
    const time              = getFromWatchedMainVals(watchedMainValues, 'time', MAIN_WATCHED_FIELDS)

    useUpdateValues({ valuesPassed, dispatch, setValue, getValues, })

    useEffect(() => {
        dispatch( sidebar_viewTimeUPD(time) )
        return () => dispatch(sidebar_viewTimeUPD(null))
    }, [dispatch, time,])


    useEffect(() => {
        handleWatch({
            values       : watchedMainValues,
            watchedFields: MAIN_WATCHED_FIELDS,
            initialValues,
            setValue,
        })
    }, [watchedMainValues, initialValues, setValue])


    console.log('BER RENDER', initialValues, getValues())
    // formState.errors && console.warn('BER RENDER BF ERRORS', formState.errors)//, getValues('submitOptions'))
    const formProps = {
        handleSubmit,
        register,
        getValues,
        setValue,
        trigger,
        control,
        formState,

        setFlyerInput,
        SUBMIT_BUTTONS_TEXT,
        time,
        flyersData,
        flyerInput,
        bookingEditorModal,
    }

    return <BookingEditorBaseForm
        formProps={formProps}
    />
}

export default BookingEditor
