function doTdArr({ minTime, bokingsAllDataObj, daysNs, tdBookingEd }) {
    const NOW_DATE_N    = _Date.dateMsToDateN(Date.now())
    let tdArr           = Array.from(TIME_SLOTS, () => [])
        tdArr.unshift([])

    daysNs.forEach( (dayN, iDay) => {
        const dayBokingData = bokingsAllDataObj[dayN]
        if (!dayBokingData) { return}
        let dayObj  = _Date.dayNumToCustomDateObj(+dayN)
        let HDayStr = dayObj.HDay ? 'HDay' : ''
        let dateStr = `${dayObj.dayN}/${dayObj.monthN}`
        let isPast  = dayN < NOW_DATE_N
        let ci      = iDay+1
        tdArr[0][ci] = (
            <td key={ci}
                className={ `td-header ${HDayStr} ${isPast ? 'isPast' : ''}`}
            >
                {dateStr}
            </td>)

        adjRows({ci, dayBokingData, tdArr, HDay : HDayStr, isPast, minTime, tdBookingEd})
    })
    return tdArr
}

function adjRows({ci, dayBokingData, tdArr, HDay, isPast, minTime, tdBookingEd}) {
    let curDayTimeSlots    = Object.keys(dayBokingData)
    curDayTimeSlots.forEach( (slot, iSlot) => {
        let ri             = iSlot+1
        let tdID           = 'r' +ri +'c' +ci
        let freeTimeVal    = 30 - dayBokingData[slot][1]
        let isInBED        = tdID == tdBookingEd
        let tdClass        = getTdClassName({freeTimeVal, minTime, ri, HDay, isPast, isInBED})
        tdArr[ri][ci] = (
            <td key       = {tdID}
                id        = {tdID}
                className = {tdClass || null}
            >
                {freeTimeVal}
            </td>
            )
    })
}// end for rows




const Tr0 = ({children}) => {
    return (
        <tr className="tableHeader">
            <th id="r0c0" className="th0">Slot</th>
            {children}
        </tr>
    )
}

function TrSlot( {ri, thText, children } ) {
    let groupClass = getTrClassName(ri)
    return (
        <tr className={groupClass}>
            <th className={groupClass} id={`r${ri}c0`}>{thText}</th>
            {children}
        </tr>
    )
}

const useGetReqDaysData = (datesOffsetArr = FT_DATES_OFFSET) => {
    const reqDateStr  = useSelector(SELECTORS.period, shallowEqual).reqDate
    const recDateMs   = new Date(reqDateStr).getTime() || Date.now()
    const reqDateN    = _Date.dateMsToDateN(recDateMs)
    const tableDateN  = useSelector(SELECTORS.ftTableDateN) || reqDateN
    const daysNs      = getDaysNsArr(tableDateN, datesOffsetArr)

    return {reqDateN, tableDateN, daysNs}
}



function FreetimeTable() {
    const timeSlots         = TIME_SLOTS

    const dispatch          = useDispatch()
    // const ADMIN_TOGGLE      = useSelector( SELECTORS.userObj ).role === 'admin'
    const minTime           = useSelector( SELECTORS.minTime )
    const bokingsAllDataObj = useSelector( SELECTORS.bookingsObj )
    const { tableDateN,
            reqDateN,
            daysNs}         = useGetReqDaysData()
    const sideBarTime       = useSelector( SELECTORS.sidebar_viewTime)
    let  tdBookingEd        = useJumpToSidebarViewTime(sideBarTime, daysNs, dispatch)


    useEffect(() => {
        dispatch( ftTableDateUPD(reqDateN) )
    }, [reqDateN, dispatch])


    useBus(
        '@@ui/NAV_BTN_CLICK',
        (ev) => {
            let targetDateN = ev.payload === 'next'
                            ? tableDateN + 14
                            : tableDateN - 14
            dispatch( ftTableDateUPD(targetDateN) )
        },
        [tableDateN]
    )


    let tdArr = doTdArr({minTime, bokingsAllDataObj, daysNs, tdBookingEd})

    //Init first/header row
    let trArr = [<Tr0 key="tr0">{tdArr[0]}</Tr0>]

    //Init data rows
    timeSlots.forEach( (slot, iSlot) => {
        let timeHeader    = timeSlots[iSlot][2]
        trArr[iSlot+1]    = <TrSlot
                                ri     = { iSlot + 1 }
                                thText = { timeHeader }
                                key    = { timeHeader }
                            >
                                {tdArr[iSlot + 1]}
                            </TrSlot>
    })

    return(
        <table
            id        = "mainTable"
            className = "freetimeTable"
            onClick   = { ftOnClick }
        >
            <tbody id="freeTimeTbody">
                {trArr}
            </tbody>
        </table>
    )
}

export default FreetimeTable