
import {
    setButtonText,
}                               from 'JS/index.js'

import {
    store,
    userDataUPD,
    SELECTORS,
}                               from 'JS/store.js'



function setUser() {
  if (localStorage.getItem('name') &&
      localStorage.getItem('role')) {

    let user = {
        name     : localStorage.getItem('name'),
        ps       : localStorage.getItem('ps'),
        role     : localStorage.getItem('role'),
    }
    setButtonText('loginButton', user.name.slice(0, 6))
  }
  return
}

function getUser() {
    return SELECTORS.userObj( store.getState() )
}

// function logIn() {
//     //debugger
//      console.log('OLDLOGIN')
// }

function logOut() {
//   localStorage.removeItem('name')
//   localStorage.removeItem('ps')
//   localStorage.removeItem('role')
//   localStorage.removeItem('mail')
    localStorage.clear();

    store.dispatch( userDataUPD( {name: '', ps: '', role:'', mail:''} ) )
}
// eslint-disable-next-line no-unused-vars




export {
    getUser,
    setUser,
    // logIn,
    logOut,
}