import { useWatch } from "react-hook-form"

export function SubmitBlock({ control, setValue, formState, buttonsText }) {
    const submitOptions = useWatch({
        control,
        name: 'submitOptions'
    }) || {}

    const { isDirty, isValid } = formState
    //debugger
    console.log('SMBLOCK: ', submitOptions, isDirty, isValid)

    const onClick = e => setValue('sumbitChoice', e.target.name)

    const SubmitButtons = Object.entries(submitOptions).map(
        ([option, isSet]) => isSet &&
            <button disabled={!isValid}
                type='submit'
                key={option}
                name={option}
                onClick={onClick}
            >
                {buttonsText[option]}
            </button>
    );

    return <div id="SubmitBlock">
        {/* {renderCnt}<br /> */}
        {SubmitButtons}
    </div>
}


