import { useEffect,
         useState,
         useRef, }              from 'react'

import { useSelector,
         useDispatch, }         from 'react-redux'

import { useForm, }             from 'react-hook-form'

import { SELECTORS,}            from 'index'

import { sidebar_viewTimeUPD, } from 'JS/store'


import { _Date, }               from 'JS/_Date'

import { findFlyerData,
         FLYERS_ED_DATACHECK, } from '../FlyersEditor'




import { GET_INITIAL_VALUES,
         getFromWatchedMainVals,
         parseDateTime,

         handleWatchLight,
         useUpdateValues,
         useSetInitialValues, } from './bedHelpers'

import { BookingEditorBaseForm }from './BookingEditorBaseForm'




const SUBMIT_BUTTONS_TEXT = {
    toCancel : 'Запрос на отмену',
    toMove   : 'Запрос на перенос',
    toChange : 'Запрос на измену',
    toNew    : 'Запрос на новое'
}

const DEFAULT_BOOKING_VALS = {
    bookingId: '',
    history:   '',
    time:      0,
    timeVal:   15,
    tarif:     'Спорт',
    fullname:  '',
    notes:     '',
    status:    'Запрос',
    flyerId:   0,
    email:     '',
    phone:     '',
}


const MAIN_WATCHED_FIELDS = ['bookingId', 'flyerId', 'time', 'timeVal', 'tarif', 'status', 'notes']

const GET_INITIAL_VALUES_LIGHTMODE = (valuesPassed, defaultValues) => {
    let lightSet = GET_INITIAL_VALUES(valuesPassed, defaultValues)
    let debugSlot = parseDateTime(lightSet.time)
    lightSet.submitOptions = {toNew: true}
    return lightSet
}


const useFlyersDataUpd = ({ dispatch, setValue, trigger, flyersData, flyerInput }) => {
    useEffect(() => {

        const flyerId = flyerInput.flyerId
        if ( flyerId === 'TOSET')                          return
        if ( !FLYERS_ED_DATACHECK.flyerId(flyerId) )       return
        if ( !FLYERS_ED_DATACHECK.flyersData(flyersData) ) return

        const flyerData = findFlyerData({ flyerId, flyersData })
        flyerData.forEach(
            field => setValue(field[0], field[1])
        )
        trigger('fullname')

    }, [dispatch, setValue, trigger, flyersData, flyerInput])
}




function BookingEditorLight() {
    // const renderCnt          = useRendersCount()
    const dispatch           = useDispatch()
    const flyersData         = useSelector(SELECTORS.flyersDataRaw)

    const valuesPassed       = useSelector(SELECTORS.sidebar_curObj)
    const initialValues      = useSetInitialValues( valuesPassed,
                                                    GET_INITIAL_VALUES_LIGHTMODE,
                                                    DEFAULT_BOOKING_VALS )
    const bookingEditorModal = useSelector(SELECTORS.booking_showModal)

    const { register,
            control,
            trigger,
            setValue,
            getValues,
            watch,
            handleSubmit,
            formState, }  = useForm({defaultValues: initialValues, mode: 'all'})

    const [flyerInput,
           setFlyerInput] = useState({ flyerId:  initialValues.flyerId,
                                       fullname: initialValues.fullname })

    register('flyerId', { validate: value => value !== 'TOSET' })

    const watchedMainValues = watch(MAIN_WATCHED_FIELDS)
    const time              = getFromWatchedMainVals(watchedMainValues, 'time', MAIN_WATCHED_FIELDS)


    useUpdateValues({ valuesPassed, dispatch, setValue, getValues, })

    useFlyersDataUpd({ flyersData, flyerInput, dispatch, setValue, trigger, })


    useEffect(() => {
        dispatch( sidebar_viewTimeUPD(time) )
        return () => dispatch(sidebar_viewTimeUPD(null))
    }, [dispatch, time,])


    useEffect(() => {
        handleWatchLight({
            values       : watchedMainValues,
            watchedFields: MAIN_WATCHED_FIELDS,
            initialValues,
            setValue,
        })
    }, [watchedMainValues, initialValues, setValue])


    const formProps = {
        handleSubmit,
        register,
        getValues,
        setValue,
        trigger,
        control,
        formState,

        setFlyerInput,
        SUBMIT_BUTTONS_TEXT,
        time,
        flyersData,
        flyerInput,
        bookingEditorModal,
    }

    return <BookingEditorBaseForm
        formProps={formProps}
    />
}


export default BookingEditorLight







